import React from 'react'
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

const WhyIcon = () => {
    const data = useStaticQuery(
      graphql`
        query {
          file(relativePath: { eq: "mage-mastery-icon.png" }) {
            childImageSharp {
              fixed(width: 30) {
                 ...GatsbyImageSharpFixed
              }
            }
          }
        }
      `
    )

    return <Img fixed={data.file.childImageSharp.fixed} width="30" alt="Why learn with Mage Mastery"/>
}

export default WhyIcon
