import React from "react"

import Layout from '../components/layout'
import SEO from '../components/seo'
import {Link, graphql} from 'gatsby'
import Img from "gatsby-image"
import WhyIcon from "../components/courses/why-icon";

import '../styles/courses.scss'

export default function CoursesPage({data}) {
    const magento2CourseImage = data.magento2CourseImageQuery.childImageSharp.fixed
    const phpLoginCourseImage = data.phpLoginCourseImageQuery.childImageSharp.fixed

    const {CoursePageQuery: coursePage} = data
    const {lessonsQuery: lessons} = data
    const {coursesQuery: courses} = data

    let courseTime = [];
    let lessonsCount = [];

    lessons.edges.forEach(lesson => {
        const course = lesson.node.frontmatter.course;
        const time = lesson.node.frontmatter.time;

        if (undefined === lessonsCount[course]) {
            lessonsCount[course] = 0;
        }
        lessonsCount[course] = lessonsCount[course] + 1;


        if (undefined === courseTime[course]) {
            courseTime[course] = 0;
        }
        courseTime[course] = courseTime[course] + time;
    })

    for (const [key, value] of Object.entries(courseTime)) {
        const minutes = Math.floor(value / 60);
        courseTime[key] = str_pad_left(minutes,'0',2)+'min';
    }

    function str_pad_left(string, pad, length) {
        return (new Array(length+1).join(pad)+string).slice(-length);
    }

    return (
      <Layout>
        <SEO title={coursePage.frontmatter.title} description={coursePage.frontmatter.description}/>
        <div className="courses-header pb-5 pt-5">
            <div className="container text-center">
                <h1>{coursePage.frontmatter.title}</h1>
                <p className="lead">{coursePage.frontmatter.subtitle}</p>
            </div>
        </div>
          <div className="container mt-5 mb-5">
              <div dangerouslySetInnerHTML={{__html: coursePage.html}} />
          </div>
        <div className="container mb-5 mt-5">
            {courses.edges.map(course => (
                <div key={course.node.id} className="course pt-4 pb-4 pl-4 pr-4 mb-5">
                    <div className="row">
                        <div className="col-md-4 mb-2">
                            <div className="course-image">
                                {
                                    course.node.frontmatter.code === 'magento-2-beginner' ?
                                      <Img fixed={magento2CourseImage} height="120" width="120"/> :
                                      course.node.frontmatter.code === 'php-login-logout' ?
                                        <Img fixed={phpLoginCourseImage} height="120" width="120"/> :
                                        <Img fixed={phpLoginCourseImage} height="120" width="120"/>
                                }
                            </div>
                        </div>
                        <div className="col-md-8">
                            <div className="price">{course.node.frontmatter.price}</div>
                            <Link key={course.node.id}
                                  className="text-white"
                                  to={course.node.frontmatter.path}
                            ><h2>{course.node.frontmatter.title}</h2></Link>
                            <div className="row">
                                {
                                    lessonsCount[course.node.frontmatter.code] > 0 ?
                                      <div className="col-md-3">
                                          <div className="level row ml-0 timing">
                                              <span className="material-icons mr-1">description</span>
                                              <div className="">
                                                  {lessonsCount[course.node.frontmatter.code]}
                                                  {lessonsCount[course.node.frontmatter.code] > 1 ? ' lessons' : ' lesson'}</div>
                                          </div>
                                      </div> : ''
                                }
                                <div className="col-md-3">
                                    <div className="level row ml-0 timing">
                                        <span className="material-icons mr-1">equalizer</span>
                                        <div className="">{course.node.frontmatter.level}</div>
                                    </div>
                                </div>
                                {
                                    courseTime[course.node.frontmatter.code] ?
                                    <div className="col-md-4">
                                        <div className="row ml-0 mb-3 timing">
                                            <span className="material-icons mr-1">access_time</span>
                                            <div className="">{courseTime[course.node.frontmatter.code]}</div>
                                        </div>
                                    </div> :
                                    ''
                                }
                            </div>
                            <p>{course.node.frontmatter.description}</p>
                        </div>
                    </div>
                </div>
            ))}
        </div>
          <div className="container text-center">
              <WhyIcon />
              <h3 className="mt-3 mb-5"> Why learn with Mage Mastery?</h3>
              <div className="row mb-5">
                  <div className="col-md-4 text-left">
                      <h4>Advance your career</h4>
                      <p>Invest in yourself and your career by staying up to date on the latest e-commerce development trends, programming best practices, and more.</p>
                  </div>
                  <div className="col-md-4 text-left">
                      <h4>Learn from Experts</h4>
                      <p>Our instructors have years of development and educational experience and they are happy to share it with you in a best possible way.</p>
                  </div>
                  <div className="col-md-4 text-left">
                      <h4>Gain experience</h4>
                      <p>Practice daily together with Mage Mastery and gain required experience to become a world leading web software developer.</p>
                  </div>
              </div>
          </div>
      </Layout>
    )
}

export const pageQuery = graphql`
  query IndexQuery {
      mageMasteryLogo: file(relativePath: { eq: "mage-mastery-icon.png" }) {
        childImageSharp {
          fluid(maxWidth: 800) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    CoursePageQuery: markdownRemark(
        frontmatter: { 
          path: {eq: "/courses"}
        }
      ) {
        html
        frontmatter {
          title
          path
          description
          subtitle
        }
      }
    magento2CourseImageQuery: file(relativePath: { eq: "courses/magento-2-beginner.png" }) {
        childImageSharp {
          fixed(width: 100) {
             ...GatsbyImageSharpFixed
          }
        }
      }
      phpLoginCourseImageQuery: file(relativePath: { eq: "courses/php-login.png" }) {
        childImageSharp {
          fixed(width: 200) {
             ...GatsbyImageSharpFixed
          }
        }
      }
    coursesQuery: allMarkdownRemark(
      filter: { 
        frontmatter: { 
          type: {eq: "course"}
        }
      },
      sort: {
        fields: [frontmatter___order],
        order: ASC
      }
    ) {
      edges {
        node {
          id
          frontmatter {
            title
            path
            description
            price
            level
            code
          }
        }
      }
    }
    lessonsQuery: allMarkdownRemark (
      filter: {
        frontmatter: {
          type: {
            eq: "lesson"
          }
        }
      }
    ) {
      edges {
        node {
          frontmatter {
            title
            time
            course
          }
        }
      }
    }
  }
`
